import axios from '@/utils/uniAxios.js'
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		// uniCard
	},
	data() {
		return {
			shoucang: 0,
			zuji: 0,
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			// abImg: '../../static/banner_01.png',
			goodsList: [],
			authShow: true,
			isCanUse: uni.getStorageSync('isCanUse') || true //默认为true
		}
	},
	onLoad(option) {
		this.loadData();
		if (uni.getStorageSync('hasLogin')) {
			this.authShow = false
		} 
		this.hotList()
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		// 好货推荐
		hotList(){
			axios.post('/home/list').then(res=>{
				this.goodsList = res.data.data.hotProductList
			})
		},
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: (infoRes) => {
					this.login(infoRes.userInfo)
				}
			})
		},

		//登录
		login(userInfo) {
			uni.showLoading({
				title: '登录中...'
			});
			const {
				city,
				gender,
				avatarUrl,
				nickName,
				phone
			} = userInfo;
			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: (loginRes) => {
					console.log(loginRes, 'loginRes');
					let code = loginRes.code;
					axios.post('/sso/user/getOpenId', {
						code
					}).then(({
						data
					}) => {
						//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						axios.post('/sso/user/miniLogin', {
							city,
							gender,
							icon: avatarUrl,
							nickname: 'nickName',
							"wxAppid": "wx35cb9f6acb94bd15",
							"wxOpenid": data.data.openId
						}).then((res) => {
							const response = res.data;
							console.log(response, 'response')
							if (response.code == 200) {
								this.authShow = false;
								this.$store.commit('login', userInfo);
								uni.setStorageSync('hasLogin', true);
								//openId、或SessionKdy存储//隐藏loading
								uni.setStorageSync('gt', response.data.token);
								uni.hideLoading();
								// this.close();
							}
						})
					})
				}
			});
		},
		// 账号管理-个人资料
		accountManage() {
			// 手机号登录，或者微信登录，不是第一次登录
			if (uni.getStorageSync('setPhone') || uni.getStorageSync('gt') || !uni.getStorageSync('isCanUse')) {
				uni.navigateTo({
					url: '../set/set?id=' + this.userInfo.id
				})
			} else {
				uni.navigateTo({
					url: '../public/login'
				})
			}
		},
		// 麦吉配送
		deliveryClick() {
			if (uni.getStorageSync('deliveryPhone')) { //已经登录过
				uni.navigateTo({
					url: '../delivery/set'
				})
			} else {
				uni.navigateTo({
					url: '../delivery/delivery'
				})
			}
		},
		// 从登录页跳转过来，刷新页面,获取用户信息
		loadData() {
			// 优先使用微信登录
			let hasLogin = this.$store.state.hasLogin;
			if (hasLogin) {
				this.authShow = false;
				console.log(7777)
				let id = ''
				axios.post('/sso/user/userInfo').then(res => {
					if (res.data.code == '200') {
						console.log(res)
						id = res.data.data.id;
						console.log(id)
						axios.post('/sso/user/'+id).then(response=>{
							console.log(response)
						})
					}
				})
				
				// let setUserInfo = this.$store.state.userInfo;
				return;
			} else if (uni.getStorageSync('setPhone')) { //使用手机号登陆
				axios.post('/sso/user/userInfo').then(res => {
					if (res.data.code == '200') {
						this.authShow = false;
						this.userInfo.mobile = res.data.data.phone;
						this.userInfo.id = res.data.data.id;

						this.$store.commit('login', this.userInfo)
					}
				})
			}
		},

		// 查看更多订单
		lookOrder() {
			uni.navigateTo({
				url: '../order/order?state='
			});
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			console.log(this.hasLogin)
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
